import React, { useEffect, useState } from 'react';
import {MapPin, Mail} from 'react-feather';
import {Octokit} from "@octokit/rest"

/**
 * O que falta?
 * - Componente do Carrossel (envolve lgica)
 * - Toda a parte do Painel (envolve lgica)
 * - Detalhes do estilo (sombras, alinhamentos, detaques...)
 * - Hover dos cones e de outros componentes
 * - Conexo com API do GitHub (necessrio para iniciar o Painel)
 * - Lgica do Toggle 
 * - Lgica do Header (fixo quando a pgina faz scroll)
 * - Movimentao da pgina por meio do header
 */

import logoHorizontal from '../assets/logos/elas_horizontal.png';
import logoVertical from '../assets/logos/elas_vertical.png';
import instagram from '../assets/redes-sociais/instagram.svg';
import github from '../assets/redes-sociais/github.svg';
import twitter from '../assets/redes-sociais/twitter.svg';
import cabecalho from '../assets/images/cabecalho.png';
import cafeComElas from '../assets/images/cafe-com-elas.png'

import icons from '../enums/icons'
import socialNetwork from '../enums/social-network'
import langs from '../enums/langs'

import Section from '../components/Section';
import ProjectCard from '../components/ProjectCard';
import ContactCard from '../components/ContactCard';
import IconStatus from '../components/IconStatus';
import Dropdown from '../components/DropDown';
import PictureLink from '../components/PictureLink';

//import getUsers from '../connections/github';

import '../styles/home.css'

export default function Home() {

  const [users, setUsers] = useState([]);

  useEffect(() => {
    var result:any = [];

    const octokit = new Octokit();
    
    octokit.orgs.listMembers({
      org: "elasComputacao",
      type: "public",
    }).then(({data}) => {
        setUsers(data);
    })
  }, [])

  return (
    <div id="home-page">
      <header>
        <a href="#">
          <img src={logoHorizontal} alt="Logo Elas@Computao Horizontal"/>
        </a>
        <ul>
          <li>
            <a href="#Sobre">Sobre</a>
          </li>
          <li>
            <a href="#Painel">Painel</a>
          </li>
          <li>
            <a href="#Projetos">Projetos</a>
          </li>
          <li>
            <a href="#Contato">Contato</a>
          </li>
          <li>
            <Dropdown 
            title="Idiomas"
            items={langs}
            />
          </li>
        </ul>
      </header>
      <main>
        <img className="wellcome-image" src={cabecalho} alt="Elas@Computao UFCG"/>
        <Section title="Sobre" className="section-about">
          <p className="screen-paragraph">
          O Elas@computao surge com o objetivo de reunir mulheres 
          do curso de Cincia da Computao da Universidade Federal 
          de Campina Grande e tambm outras mais. Nosso foco  levantar, 
          discutir e impulsionar os assuntos que dizem respeito ao feminino, 
          feminismo, nossos espaos, falas, movimentos e necessidades. A nossa
          misso  inspirar mulheres mostrando que todas so capazes de trabalhar
          e serem as melhores profissionais na rea que Elas quiserem. Somos uma
          comunidade que se apoia e que est disposta sempre a acolher novas 
          membras e continuamente ajud-las a se desenvolverem como mulheres 
          e profissionais.
          </p>
          <IconStatus 
            icon={logoVertical} 
            status={icons.yellowRibbon} 
            statusText="
            O Elas@Computao apoia a campanha do Setembro Amarelo em preveno
            ao suicdio.
            Se voc estiver passando por problemas relacionados a sua sade mental 
            ou conhece algum que est passando por alguma dificuldade, procure ajuda
            profissional.
            "
          />
        </Section>
        <Section toggle title="Painel" className="section-painel">
          {
            users.map(user => {
              return(
                <PictureLink 
                  href={`${socialNetwork.github}/${user.login}`}
                  pic={user.avatar_url}
                  text={user.login}
                  key={user.id}
                />
              );
            })
          }
        </Section>
        <Section title="Projetos" className="section-projects">
          <ProjectCard 
          imageURL={cafeComElas} 
          title="Caf com Elas" 
          description="Conversas e palestras das mulheres de computao. 
          Com o objetivo de trazer conversas sobre temas interessantes 
          e importantes acerca de nossa rea e papel, com o intuito de 
          abraar e estimular mulheres a participarem na comunidade."
          href="https://github.com/elasComputacao/CafeComElas"
          />
          <ProjectCard 
          imageURL={cafeComElas} 
          title="Caf com Elas" 
          description="Conversas e palestras das mulheres de computao. 
          Com o objetivo de trazer conversas sobre temas interessantes 
          e importantes acerca de nossa rea e papel, com o intuito de 
          abraar e estimular mulheres a participarem na comunidade."
          href="https://github.com/elasComputacao/CafeComElas"
          />
          <ProjectCard 
          imageURL={cafeComElas} 
          title="Caf com Elas" 
          description="Conversas e palestras de mulheres de computao. 
          Com o objetivo de trazer conversas sobre temas interessantes 
          e importantes acerca de nossa rea e papel e 
          estimular mulheres a participarem na comunidade."
          href="https://github.com/elasComputacao/CafeComElas"
          />
        </Section>
      </main>
      <footer>
        <h1 id="Contato">Contato</h1>
        <div className="footer-content">
          <div className="contact">
            <ContactCard content="Universidade Federal de Campina Grande - UFCG,
            Campus Central, R. Aprgio Veloso, 882 - Universitrio, Campina Grande - PB, 58428-830">
            <MapPin />
            </ContactCard>            
            <ContactCard content="elas@computacao.ufcg.edu.br">
              <Mail/>
            </ContactCard>
          </div>
          <div className="social-networks">
            <a target="_blank" href={socialNetwork.instagramElas}>
              <img src={instagram} alt="Instagram"/>
            </a>
            <a target="_blank" href={socialNetwork.githubElas}>
              <img src={github} alt="GitHub"/>
            </a>
            <a target="_blank" href="">
              <img src={twitter} alt="Twitter"/>
            </a>
          </div>
        </div>
        <div className="background">
        </div>
        <div className="copyright">
          <a href="#">
            <img src={logoHorizontal} alt="Logo Elas@Computao Horizontal"/>
          </a>
          <span> 2020 Elas@Computao</span>
        </div>
      </footer>
    </div>
  );
}
